<template>
  <div class="videoedit">
    <div class="devicetype">
      <p class="t">{{htitle}} <span @click="handleBack">返回</span></p>
    </div>
    <div class="form">
      <div class="item">
        <p class="t">MAC</p>
        <p><el-input v-model="info.mac" placeholder="请输入公司编码"></el-input></p>
      </div>
      <div class="item">
        <p class="t">视频流地址</p>
        <p><el-input v-model="info.url" placeholder="请输入公司编码"></el-input></p>
      </div>
      <div class="item">
        <p class="t">安装地址</p>
        <p><el-input v-model="info.address" placeholder="请输入公司编码"></el-input></p>
      </div>
      <p class="center">
        <el-button class="blue" type="primary" @click="handleSubmit">提交</el-button>
      </p>
    </div>
  </div>
</template>

<script>
import { addVideo, updateVideo } from '@/api/device'
import msgUtil from '@/utils/msgUtil'

export default {
  name: 'Videoedit',
  created() {
    console.log(this.$route.params.type)
    console.log(this.$route.params.type === 'edit')
    if (this.$route.params.type === 'edit') {
      this.info = {
        mac: this.$route.query.mac,
        url: this.$route.query.url,
        address: this.$route.query.address,
        id: this.$route.query.id
      }
    }
  },
  data() {
    return {
      info: {
        mac: '',
        url: '',
        address: '',
        id: 0
      }
    }
  },
  computed: {
    htitle() {
      return this.$route.params.type === 'edit' ? '编辑视频' : '添加视频'
    },
    isEdit() {
      return this.$route.params.type === 'edit'
    }
  },
  methods: {

    handleBack() {
      this.$router.back()
    },
    handleSubmit() {
      if (this.isEdit) {
        this.edit()
      } else {
        this.add()
      }
    },
    async edit() {
      const params = {
        url: this.info.url,
        mac: this.info.mac,
        address: this.info.address,
        id: this.info.id
      }
      try {
        const res = await updateVideo(params)
        if (res.status === 200) {
          msgUtil.info('操纵成功', '绑定视频流成功')
          this.$router.back()
        } else {
          msgUtil.error('绑定视频流失败', res.message)
        }
      } catch (error) {
        msgUtil.error('绑定视频流失败', msgUtil.findErrorMsg(error))
      }
    },
    async add() {
      const params = {
        url: this.info.url,
        mac: this.info.mac,
        address: this.info.address
      }
      try {
        const res = await addVideo(params)
        if (res.status === 200) {
          msgUtil.info('操纵成功', '绑定视频流成功')
          this.$router.back()
        } else {
          msgUtil.error('绑定视频流失败', res.message)
        }
      } catch (error) {
        msgUtil.error('绑定视频流失败', msgUtil.findErrorMsg(error))
      }

    }
  }
}
</script>


